import React, { useState, useEffect } from 'react'
import { reactLocalStorage } from 'reactjs-localstorage'
import { uniq } from 'lodash'
import { useLocation } from '@reach/router'

import {
  Container,
  Content,
  Footer,
  ReactIcon,
  SmallReactIcon,
  Border,
} from './style'

import Emoji5 from './../../../images/ask-auntie-emoji-reaction-angry.svg'
import Emoji4 from './../../../images/ask-auntie-emoji-reaction-sad.svg'
import Emoji3 from './../../../images/ask-auntie-emoji-reaction-happy.svg'
import Emoji2 from './../../../images/ask-auntie-emoji-reaction-love.svg'
import Emoji1 from './../../../images/ask-auntie-emoji-reaction-like.svg'

interface IQAItemProps {
  question: string
  answer: string
  className?: string
  like2: number
  like1: number
  like: number
  dislike: number
  hate: number
  id: string
  path: string
  onReact?(question: string, value: string): void
}

const emojiIcons: { value: string; img: string }[] = [
  {
    value: 'like2',
    img: Emoji1,
  },
  {
    value: 'like1',
    img: Emoji2,
  },
  {
    value: 'like',
    img: Emoji3,
  },
  {
    value: 'dislike',
    img: Emoji4,
  },
  {
    value: 'hate',
    img: Emoji5,
  },
]

export default ({
  question,
  answer,
  className,
  path,
  like2,
  like1,
  like,
  dislike,
  hate,
  id,
  onReact = () => {},
}: IQAItemProps) => {
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  const [reactedAction, setReactedAction] = useState(0)
  const [reactions, setReactions] = useState<string[]>([])

  const composeDisplayEmoticons = value => {
    let temp = []
    if (value) {
      temp.push(value)
    }

    if (like2 > 0) temp.push('like2')
    if (like1 > 0) temp.push('like1')
    if (like > 0) temp.push('like')
    if (dislike > 0) temp.push('dislike')
    if (hate > 0) temp.push('hate')
    return uniq(temp).slice(0, 2)
  }
  const handleReact = ({ id, path, value, action }) => {
    const qa = reactLocalStorage.getObject('qa')
    qa[id] = value
    reactLocalStorage.setObject('qa', qa)
    if (value !== reactedAction) {
      onReact(path, reactedAction, value, action)
      setReactions(composeDisplayEmoticons(value))
    }
    setReactedAction(value)
  }
  useEffect(() => {
    const qaData = reactLocalStorage.getObject('qa')
    if (qaData[id]) {
      setReactedAction(qaData[id])
    }

    setReactions(composeDisplayEmoticons(qaData[id]))
    return () => {}
  }, [])
  return (
    <Container className={className}>
      <Content>
        <div>{`Q: ${question}`}</div>
        <div className='ml-4 ml-sm-0'>{`A: ${answer}`}</div>
      </Content>
      <Footer>
        <div className='d-flex pl-3'>
          <div className='d-flex flex-row-reverse'>
            {emojiIcons.map(emo => {
              return (
                reactions.includes(emo.value) && (
                  <SmallReactIcon
                    key={emo.value}
                    src={emo.img}
                    onClick={() => {
                      // console.log('unlike')
                      // setReactedAction(0)
                      // let qa = reactLocalStorage.getObject('qa')
                      // console.log('qa 1', qa)
                      // qa = omit(qa, id)
                      // console.log('qa 2', qa)
                      // reactLocalStorage.setObject('qa', qa)
                      // handleReact({
                      //   id,
                      //   path,
                      //   value: emo.value,
                      //   action: 'minus',
                      // })
                    }}
                  />
                )
              )
            })}
          </div>
          <span className='my-auto ml-2'>
            {like2 + like1 + like > 0
              ? `${like2 + like1 + like + dislike + hate}`
              : ''}
          </span>
        </div>
        <Border />
        <div className='d-flex pl-3 d-flex flex-wrap'>
          <div className='d-flex mt-2 mr-3'>
            <span className='my-auto'>
              {isFr
                ? 'Cette ressource était-elle utile?'
                : 'Was this resource helpful?'}{' '}
            </span>
          </div>
          <div className='d-flex mt-2'>
            {emojiIcons.map(({ img, value }) => {
              return (
                <ReactIcon
                  key={value}
                  className='mr-3 my-auto'
                  onClick={() =>
                    handleReact({ id, path, value, action: 'plus' })
                  }
                  src={img}
                />
              )
            })}
          </div>
        </div>
      </Footer>
    </Container>
  )
}
