import React from 'react'

import { FaSearch } from 'react-icons/fa'
import {
  Container,
  InputContainer,
  SearchIcon,
  DropDownContent,
  DropDownItem,
} from './style'

interface ISearchInputProps {
  value: string
  data: Array<any>
  // eslint-disable-next-line no-unused-vars
  onChange: (arg0: string) => void
  onItemClick: (arg0: string) => void
}

export default ({ value, data, onChange, onItemClick }: ISearchInputProps) => {
  return (
    <Container>
      <div className='position-relative'>
        <InputContainer
          value={value}
          onChange={e => onChange(e.target.value)}
        />
        <SearchIcon>
          <FaSearch size={32} />
        </SearchIcon>
        <DropDownContent>
          {data
            .filter(d => d.node.frontmatter.question.includes(value))
            .map(
              ({
                node: {
                  frontmatter: { question },
                },
              }) => {
                return (
                  <DropDownItem
                    key={question}
                    onClick={() => onItemClick(question)}
                  >
                    {question}{' '}
                  </DropDownItem>
                )
              }
            )}
        </DropDownContent>
      </div>
    </Container>
  )
}
