import React, { useState } from 'react'
import { Accordion } from 'react-bootstrap'
import { FaPlus } from 'react-icons/fa'
import ReactHTMLParser from 'react-html-parser'

import {
  ToggleIcon,
  AccordionItemHeader,
  AccordionItem,
  Content,
  AccordionItemHeaderContent,
} from './style'

interface IAccordionProps {
  background?: string
  className?: string
  data: {
    title: string
    content: string
  }[]
}

export default ({ data, className, background = 'light' }: IAccordionProps) => {
  const [current, setCurrent] = useState({})

  return (
    <div className={className}>
      <Accordion defaultActiveKey='0'>
        {data.map((item, index) => {
          return (
            <AccordionItem key={`${index}key`}>
              <Accordion.Toggle
                as={AccordionItemHeader}
                onClick={() => setCurrent(current === item ? {} : item)}
                eventKey={`${index + 1}`}
              >
                <ToggleIcon type={background} active={current === item}>
                  <FaPlus size={24} />
                </ToggleIcon>
                <AccordionItemHeaderContent
                  active={current === item}
                  className='ml-2'
                >
                  {item.title}
                </AccordionItemHeaderContent>
              </Accordion.Toggle>

              <Accordion.Collapse eventKey={`${index + 1}`}>
                <Content>{ReactHTMLParser(item.content)}</Content>
              </Accordion.Collapse>
            </AccordionItem>
          )
        })}
      </Accordion>
    </div>
  )
}
