import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { useLocation } from '@reach/router'
import { Container, ItemsContainer } from './style'
import SearchInput from './../SearchInput'
import QAItem from './../QAItem'
import { Button } from './../../../components'

const PAGE_SIZE = 3
interface IQASectionProps {
  title: string
  data: {
    node: {
      frontmatter: {
        language: string
        question: string
        answer: string
        like2: number
        like1: number
        like: number
        dislike: number
        hate: number
      }
      id: string
      fileAbsolutePath: string
    }
  }[]
}

export default ({ title, data }: IQASectionProps) => {
  const [currentPage, setCurrentPage] = useState<number>(1)
  const [searchResults, setSearchResults] = useState<any>([])
  const [searchValue, setSearchValue] = useState('')
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  const onNextPage = () => {
    setCurrentPage(currentPage + 1)
  }

  const handleReact = async (
    path: string,
    preValue: string,
    value: string,
    action: string
  ) => {
    const folderName = path.split('/')[path.split('/').length - 2]
    const content = await axios.get(
      `https://gitlab.com/api/v4/projects/22180390/repository/files/content%2Fquestions%2F${folderName}%2Findex.md/raw?ref=master`,
      {
        headers: {
          'PRIVATE-TOKEN': 'CZiBaqQd5NGDyzLEpPxw',
        },
      }
    )

    let template = {
      question: '',
      email: '',
      answer: '',
      like2: 0,
      like1: 0,
      like: 0,
      dislike: 0,
      published: false,
    }
    content.data
      .split(/\n/)
      .map(d => d.split(': '))
      .forEach(item => {
        if (item.length > 1) {
          template[item[0]] =
            item[0] === 'like' ||
            item[0] === 'like1' ||
            item[0] === 'like2' ||
            item[0] === 'dislike' ||
            item[0] === 'hate'
              ? parseInt(item.slice(1).join(''))
              : item
                  .slice(1)
                  .join(': ')
                  .trim()
        }
      })
    console.log(
      'content',
      content.data,
      content.data.split(/\n/),
      content.data.split(/\n/).slice(1, content.data.split(/\n/).length - 2),
      template
    )

    if (action === 'plus') {
      template[value] = template[value] + 1
      if (preValue) {
        template[preValue] = template[preValue] > 0 ? template[preValue] - 1 : 0
      } else {
      }
    } else {
    }

    let chunks = Object.keys(template).map(key => {
      return key === 'question' || key === 'answer' || key === 'email'
        ? `${key}: ${template[key]}`
        : `${key}: ${template[key]}`
    })

    chunks = ['---', ...chunks, '---']

    console.log('updated content', chunks.join('\n'))
    axios
      .put(
        `https://gitlab.com/api/v4/projects/22180390/repository/files/content%2Fquestions%2F${folderName}%2Findex.md`,
        {
          branch: 'master',
          author_email: 'support@deplume.ca',
          author_name: 'User_Submission',
          content: chunks.join('\n'),
          commit_message: `Reacted on question ${folderName}.`,
        },
        {
          headers: {
            'PRIVATE-TOKEN': 'CZiBaqQd5NGDyzLEpPxw',
          },
        }
      )
      .then(res => {})
      .catch(err => {})
  }
  const handleItemClick = params => {
    console.log('item click', params)
    setSearchResults(data.filter(d => d.node.frontmatter.question === params))
  }
  useEffect(() => {
    setSearchResults(
      data
        .filter(d => d.node.frontmatter.language === (isFr ? 'Fr' : 'En'))
        .reverse()
        .slice(0, 5)
    )
    return () => {}
  }, [])
  return (
    <Container>
      <div className='container'>
        <h1 className='text-center'>{title}</h1>
        <div className='mt-5'>
          <h3>
            {isFr
              ? 'Rechercher les questions et réponses précédentes'
              : 'Search previous questions and answers'}{' '}
          </h3>
          <SearchInput
            value={searchValue}
            onChange={setSearchValue}
            data={data.filter(
              d => d.node.frontmatter.language === (isFr ? 'Fr' : 'En')
            )}
            onItemClick={handleItemClick}
          />
          <ItemsContainer>
            {searchResults.slice(0, 5).map(
              (
                {
                  node: {
                    frontmatter: {
                      question,
                      answer,
                      like2,
                      like1,
                      like,
                      dislike,
                      hate,
                    },
                    id,
                    fileAbsolutePath,
                  },
                },
                index
              ) => {
                return (
                  <QAItem
                    key={`${index}key`}
                    question={question}
                    answer={answer}
                    like2={like2}
                    like1={like1}
                    like={like}
                    dislike={dislike}
                    hate={hate}
                    id={id}
                    path={fileAbsolutePath}
                    onReact={handleReact}
                  />
                )
              }
            )}
          </ItemsContainer>
        </div>
        {/* {searchResults.length > currentPage * PAGE_SIZE && (
          <div className='d-flex justify-content-center mt-5'>
            <Button background='light' onClick={() => onNextPage()}>
              Load More
            </Button>
          </div>
        )} */}
      </div>
    </Container>
  )
}
