import React, { useState } from 'react'
import { Form, Formik } from 'formik'
import * as Yup from 'yup'
import axios from 'axios'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import { useLocation } from '@reach/router'
import { Container, SubmitSection, FAQSection, Message } from './style'
import {
  FormInput as Input,
  FormTextarea as Textarea,
  Button,
  Recaptcha,
} from './../../../components'
import Accordion from './../FAQAccordion'
import { format } from 'date-fns'
import { encodeForm } from './../../../utils'

interface IProps {
  submitTitle: string
  data: any
}
export default ({ submitTitle, data }: IProps) => {
  const [result, setResult] = useState('')
  const md = new Remarkable()
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')
  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .email(
        isFr ? 'Entrez une adresse courriel valide.' : 'Must be a valid email'
      )
      .required(
        isFr
          ? 'Votre courriel est un champ obligatoire.'
          : 'Your Email is required'
      ),
    question: Yup.string().required(
      isFr ? 'Votre question est un champ obligatoire.' : 'Question is required'
    ),
    recaptcha: Yup.boolean().required(
      isFr
        ? "Veuillez vérifier que vous n'êtes pas un robot."
        : 'Please verify you are not a robot.'
    ),
  })
  return (
    <Container>
      <SubmitSection>
        <Formik
          initialValues={{
            email: '',
            question: '',
            answer: '',
            like: '0',
            like1: '0',
            like2: '0',
            dislike: '0',
            hate: '0',
          }}
          validationSchema={validationSchema}
          onSubmit={(values, { resetForm }) => {
            let temp = ['---']
            temp = [
              ...temp,
              ...Object.keys(values).map(key => {
                return key === 'question'
                  ? `${key}: "${values[key]
                      .split('\n')
                      .join('     \r\n\r\n  ')}"`
                  : `${key}: ${values[key]}`
              }),
            ]
            temp.push(isFr ? 'language: Fr' : 'language: En')
            temp.push('---')
            axios
              .post(
                `https://gitlab.com/api/v4/projects/22180390/repository/files/content%2Fquestions%2F${format(
                  new Date(),
                  'MM-dd-yyyy-HH-mm-ss'
                )}%2Findex.md`,
                {
                  branch: 'master',
                  author_email: 'support@deplume.ca',
                  author_name: 'User_Submission',
                  content: temp.join('\n'),
                  commit_message: `Question ${values.question} is created.`,
                },
                {
                  headers: {
                    'PRIVATE-TOKEN': 'CZiBaqQd5NGDyzLEpPxw',
                  },
                }
              )
              .then(res => {
                fetch('https://generation4equality.ca/ask', {
                  method: 'POST',
                  headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                  },
                  body: encodeForm({
                    'form-name': 'question',
                    ...values,
                  }),
                })
                  .then(() => {
                    setResult(
                      isFr
                        ? 'Merci pour votre question.'
                        : 'Thank you for submitting a question.'
                    )
                    resetForm()
                  })
                  .catch(error => {
                    console.log('error', error)
                    setResult(isFr ? "Désolé, une erreur s\'est produite. Veuillez réessayer ultérieurement." : "Sorry, an unexpected error occurred. Please try again.")
                  })
              })
              .catch(err => {
                setResult(
                  isFr
                    ? "Désolé, une erreur s'est produite. Veuillez réessayer ultérieurement."
                    : 'Sorry, an unexpected error occurred. Please try again. '
                )
              })
          }}
        >
          {() => (
            <Form data-netlify={true} name='question'>
              <div>{ReactHTMLParser(md.render(submitTitle))}</div>
              <h4 className='mt-5'>
                {isFr ? 'Saisis ton courriel ' : 'Enter your email'}
              </h4>
              <i className='mt-2'>
                {isFr
                  ? 'Remarque importante : Aucune de tes informations personnelles ne sera partagée. Nous avons seulement besoin de ton courriel pour te faire savoir que nous avons reçu ta question et pour t’orienter vers des ressources externes, le cas échéant.'
                  : 'Important note: None of your personal information will be shared. We only need your email to let you know that we received your question and to direct you to any external resources, if applicable.'}
              </i>
              <Input name='email' className='mt-3 mb-3' />

              <h4 className='mt-4 mb-3'>
                {isFr
                  ? 'Pose une question à Matante et à des Aînés'
                  : 'Ask Auntie & Elders a question'}
              </h4>
              <Textarea name='question' className='mb-3' />
              <Recaptcha name='recaptcha' />

              {result && <Message className='mb-3'> {result}</Message>}
              <Button type='button' buttonType='submit'>
                {isFr ? 'Soumets ta question' : 'Submit Your Question'}
              </Button>
            </Form>
          )}
        </Formik>
      </SubmitSection>
      <FAQSection>
        <h2 className='mb-5'>
          {isFr ? 'Foire aux questions' : 'Frequently Asked Questions'}
        </h2>
        <Accordion
          data={data.map((d: any) => {
            return {
              ...d,
              content: md.render(d.description),
            }
          })}
        />
      </FAQSection>
    </Container>
  )
}
