import * as React from 'react'
import { graphql } from 'gatsby'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import Layout from './../layout'
import { SEO, StripeBar, Banner } from './../components'
import { TopText, QASection, FAQ } from './../containers/Ask'
import { RedSection } from './../containers/Ask/style'
import AskAuntieSpeechBubbleImg from './../images/ask-auntie-speach-bubble.svg'

interface AskPageProps {
  location: {
    pathname: string
  }
  data: any
  pageContext: {
    language: string
    title: string
    description: string
    section1: string
    section2: string
    qaTitle: string
    submitTitle: string
    seoKeyword: string
    seoDescription: string
    faq: {
      description: string
      title: string
    }[]
  }
}

export default ({
  location,
  data,
  pageContext: {
    language,
    title,
    description,
    section1,
    section2,
    qaTitle,
    submitTitle,
    faq,
    seoKeyword,
    seoDescription,
  },
}: AskPageProps) => {
  const md = new Remarkable()

  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        keyword={seoKeyword}
        description={seoDescription}
        title={title}
      />
      <Banner colors={['#397897', '#00587F', '#80D0DB']}>
        <h1>{title}</h1>
        <h2 className='mt-4'>{description}</h2>
      </Banner>
      <StripeBar colors={['#D29329', '#DA5027', '#E58630', '#D16428']} />
      <TopText image={AskAuntieSpeechBubbleImg}>
        <div className='mt-auto mb-auto'>
          {ReactHTMLParser(md.render(section1))}
        </div>
      </TopText>
      <StripeBar colors={['#00587F', '#95C5BB', '#387896', '#80D0DB']} />
      <RedSection>
        <div className='container text-center'>{section2}</div>
      </RedSection>
      <QASection title={qaTitle} data={data.allMarkdownRemark.edges} />
      <StripeBar colors={['#D29329', '#DA5027', '#E58630', '#D16428']} />
      <FAQ submitTitle={submitTitle} data={faq} />
      <StripeBar colors={['#d2932a', '#d2932a', '#d2932a', '#d2932a']} />
    </Layout>
  )
}

export const askPageQuery = graphql`
  query AskPageQuery {
    allMarkdownRemark(
      filter: {
        fileAbsolutePath: { regex: "/questions/i" }
        frontmatter: { published: { eq: true } }
      }
    ) {
      edges {
        node {
          id
          fileAbsolutePath
          frontmatter {
            email
            answer
            question
            published
            like2
            like1
            like
            dislike
            language
            hate
          }
        }
      }
    }
  }
`
